//     
export const formatAddress = (locality: string, street: string, number: string): string => {
  //     
  const expandedStreet = street
    .replace(/\b\.\s*/gi, ' ')
    .replace(/\b\.\s*/gi, ' ')
    .replace(/\b-\s*/gi, ' ')
    .replace(/\b\.\s*/gi, ' ')
    .replace(/\b\.\s*/gi, ' ')
    .replace(/\b-\s*/gi, ' ')
    .replace(/\b\.\s*/gi, ' ')
    .replace(/\b\.\s*/gi, ' ')
    .replace(/\b\.\s*/gi, ' ')
    .replace(/\b\.\s*/gi, ' ')
    .trim();

  //    (    )
  // : "50 1 15"  "50115"  "50  1  15"
  const formattedNumber = number
    .replace(/\b\.?\s*/gi, '')
    .replace(/\b\s*/gi, '')
    .replace(/\b\.?\s*/gi, '')
    .replace(/\b\s*/gi, '')
    .replace(/\b\.?\s*/gi, '')
    .replace(/\b\s*/gi, '')
    .trim();

  return `${locality}, ${expandedStreet}, ${formattedNumber}`;
};

//    
export const formatNormalizedAddress = (locality: string, street: string, number: string): string => {
  return ` : ${formatAddress(locality, street, number)}`;
};
